* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.footer {
    background: rgba(37, 37, 37, 0.972);
    color: rgb(165, 162, 162);
    margin-top: 100px;
    padding-top: 20px;
}

.footer h1 {
	margin: 60px 150px 30px;
	font-size: 40px;
}

.footer span {
	font-size: 50px;
	color: rgb(219, 0, 0);
}

.footer hr {
    background: white;
    width: 77%;
    margin: auto;
    margin-bottom: 70px;
}

.footerText {
    display: flex;
    justify-content: space-around;
    font-size: 14px;
}

.footFooter {
    display: flex;
    justify-content: space-between;
    background-color: rgba(8, 8, 8, 0.972);
    margin-top: 20px;
    padding: 10px;
}

.footerText div:nth-child(even) {
    display: flex;
    justify-content: space-around;
}

.footerText div:nth-child(even) p {
    margin: 20px 30px 0 0;
}

.footerText li {
    list-style: none;
    margin: 0 60px 14px -140px;
}

.footFooter p {
    margin: auto 20px;
    font-size: 12px;
}

.footFooter p:nth-child(2) {
color: rgb(255, 0, 85);
}

.footerText p b {
    color: rgb(250, 32, 32);
}